import React, { useState } from "react";
import NavDropdown from "react-bootstrap/NavDropdown";
import { Image } from "react-bootstrap";
import "./SideBar.css";

const SideBar = (props) => {
  let sidebar_routes = props.my_routes;
  let searchParams = props.searchParams;
  const userDetails = props.userDetails;
  const analyticsSusbscribed = props.analyticsSusbscribed;

  const getIcon = (route) => {
    console.log(route);
    switch (route) {
      case "/inventory_report":
        return <i className="bi bi-clipboard2-data"></i>;
      case "/sale_report":
        return <i className="bi bi-graph-up-arrow"></i>;
      case "/aging_report":
        return <i className="bi bi-clock-history"></i>;
      case "/price_co-pilot":
        return <i className="bi bi-clipboard2-data"></i>;
      default:
        return;
    }
  };
  const url =
    "https://images.pexels.com/photos/220453/pexels-photo-220453.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1";

  const [extendedView, setExtendedView] = useState(true);

  function toggleExtendedView() {
    setExtendedView(!extendedView);
    if (extendedView) {
      document.body.classList.add("toggle-sidebar");
    } else {
      document.body.classList.remove("toggle-sidebar");
    }
  }

  const UserMenu = (
    <div>
      {
        <Image
          src={url}
          alt="UserName profile image"
          roundedCircle
          style={{
            width: "40px",
            height: "40px",
            objectFit: "cover",
            marginRight: "20px",
          }}
        />
      }{" "}
      {userDetails.name}
    </div>
  );

  return (
    <div style={{ zIndex: "1000", position: "relative" }}>
      {/* <div
        className="extender"
        style={{
          position: "absolute",
          width: "50px",
          height: "50px",
          maxHeight: "60px",
          maxWidth: "60px",
          backgroundColor: "aliceblue",
          top: "10px",
          left: extendedView ? "270px" : "-20px",
          transition: "ease-in",
          transitionDuration: "200ms",
          transitionProperty: "left",
          zIndex: 10000,
          borderRadius: "50%",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          boxShadow: "0px 3px 17px -8px rgba(0,0,0,0.44)",
          WebkitBoxShadow: "0px 3px 17px -8px rgba(0,0,0,0.44)",
        }}
        onClick={toggleExtendedView}
      >
        {!extendedView ? (
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="30"
            height="30    "
            fill="currentColor"
            class="bi bi-arrow-bar-right"
            viewBox="0 0 16 16"
          >
            <path
              fill-rule="evenodd"
              d="M6 8a.5.5 0 0 0 .5.5h5.793l-2.147 2.146a.5.5 0 0 0 .708.708l3-3a.5.5 0 0 0 0-.708l-3-3a.5.5 0 0 0-.708.708L12.293 7.5H6.5A.5.5 0 0 0 6 8Zm-2.5 7a.5.5 0 0 1-.5-.5v-13a.5.5 0 0 1 1 0v13a.5.5 0 0 1-.5.5Z"
            />
          </svg>
        ) : (
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="30"
            height="30    "
            fill="currentColor"
            class="bi bi-arrow-bar-left"
            viewBox="0 0 16 16"
          >
            <path
              fill-rule="evenodd"
              d="M12.5 15a.5.5 0 0 1-.5-.5v-13a.5.5 0 0 1 1 0v13a.5.5 0 0 1-.5.5ZM10 8a.5.5 0 0 1-.5.5H3.707l2.147 2.146a.5.5 0 0 1-.708.708l-3-3a.5.5 0 0 1 0-.708l3-3a.5.5 0 1 1 .708.708L3.707 7.5H9.5a.5.5 0 0 1 .5.5Z"
            />
          </svg>
        )}
      </div> */}
      <aside
        id="sidebar"
        className="sidebar remove-bgs"
        style={{ zIndex: "1000" }}
      >
        <ul className="sidebar-nav" id="sidebar-nav">
          {Object.entries(sidebar_routes).map(([route, name]) => {
            let is_active_class =
              route == window.location.pathname ? "" : "collapsed";
            return (
              <li key={route} className={"nav-item- " + route}>
                <a
                  className={"nav-link " + is_active_class}
                  href={route + searchParams}
                >
                  {getIcon(route)}
                  <span>{name}</span>
                </a>
              </li>
            );
          })}
        </ul>
        <div className="profile-component">
          <NavDropdown className="non-block" title={UserMenu} id="nav-dropdown">
            <NavDropdown.Item eventKey="4.1">
              <h6>{userDetails.name ? userDetails.name : "NA"}</h6>
              <span>
                {userDetails.roles ? userDetails.roles.join(", ") : "NA"}
              </span>
            </NavDropdown.Item>
            <NavDropdown.Divider />
            <NavDropdown.Item eventKey="4.2">
              <a
                className="dropdown-item d-flex align-items-center"
                href={process.env.REACT_APP_API_URL + "/#/users/2"}
                target="_blank"
              >
                <i className="bi bi-person"></i>
                <span>My Profile</span>
              </a>
            </NavDropdown.Item>
            <NavDropdown.Divider />
            <NavDropdown.Item eventKey="4.3">
              <a className="dropdown-item d-flex align-items-center" href="#/">
                <i className="bi bi-envelope"></i>
                <span>{userDetails.email || "NA"}</span>
              </a>
            </NavDropdown.Item>
            <NavDropdown.Divider />
            <NavDropdown.Item eventKey="4.4">
              {" "}
              <a className="dropdown-item d-flex align-items-center" href="#/">
                <i className="bi bi-telephone"></i>
                <span>{userDetails.phone || "NA"}</span>
              </a>
            </NavDropdown.Item>
            <NavDropdown.Divider />
            <NavDropdown.Item eventKey="4.4">
              {" "}
              <a className="dropdown-item d-flex align-items-center" href="#/">
                <i className="bi bi-check-circle"></i>
                <span>{userDetails.state || "NA"}</span>
              </a>
            </NavDropdown.Item>
            <NavDropdown.Divider />
            <NavDropdown.Item eventKey="4.4">
              {" "}
              <a className="dropdown-item d-flex align-items-center" href="#/">
                {analyticsSusbscribed ? (
                  <>
                    {/* <i className="bi bi-bell-slash"></i> */}
                    <i className="bi bi-check-circle"></i>
                    <span>Subscribed</span>
                  </>
                ) : (
                  <>
                    {/* <i className="bi bi-bell"></i> */}
                    <i className="bi bi-clock-history"></i>
                    <span>Subscription Pending</span>
                  </>
                )}
              </a>
            </NavDropdown.Item>
          </NavDropdown>
        </div>
      </aside>
    </div>
  );
};

export default SideBar;
