import React, { Fragment, useEffect, useState } from "react";
import {
  useReactTable,
  createColumnHelper,
  flexRender,
  getCoreRowModel,
  getSortedRowModel,
  getPaginationRowModel,
  getFilteredRowModel,
} from "@tanstack/react-table";
import "./TopTable.css";
import Spinner from "react-bootstrap/Spinner";
import Cookies from "js-cookie";
import { formatter } from "../../utils/utils";
import SearchBar from "../SearchBar/SearchBar";

const colsWidth = {
  "Cert Num": "85px",
  Shape: "60px",
  Carats: "60px",
  "LD Spec": "60px",
  Color: "60px",
  Clarity: "60px",
  Cut: "60px",
  Polish: "60px",
  Sym: "60px",
  Fluor: "60px",
  Shade: "60px",
  Milky: "60px",
  Lab: "60px",
  "Eye Clean": "80px",
  BIC: "60px",
  "Rap %": "60px",
  "$/Ct": "60px",
  "Total Price": "75px",
  Video: "60px",
  "Rap Rank": "60px",
  "Rap Matching count": "60px",
  "1st Rap %": "60px",
  "5th Rap %": "60px",
  "25th Rap %": "60px",
  "50th Rap %": "60px",
  "100th Rap %": "60px",
  "Girdle Con": "85px",
  "Report Date": "85px",
  "Carat Range": "85px",
};

const rightAligned = [
  "Carats",
  "Rap %",
  "$/Ct",
  "Total Price",
  "LD Rank",
  "Rap Rank",
  "Rap Matching count",
  "LD Count",
  "1st Rap %",
  "5th Rap %",
  "25th Rap %",
  "50th Rap %",
  "100th Rap %",
  "Aging",
  "CrAng",
  "CrHeight",
  "PavAng",
  "PavDepth",
  "Ratio",
  "Carat Range",
  "Depth",
  "Table",
];

const rapDiff = [
  "1st Rap %",
  "5th Rap %",
  "25th Rap %",
  "50th Rap %",
  "100th Rap %",
];

const TopTable = ({
  setItem,
  passRef,
  id,
  openModal,
  bottomItem,
  setVideoUrl,
  setUseItemData,
}) => {
  // const [columns, setColumns] = useState()
  const [itemData, setItemData] = useState();
  const [sorting, setSorting] = useState();
  const [intItem, setIntItem] = useState();
  const [error, setError] = useState(false);
  const [errorMessage, setErrorMessage] = useState();
  const columnHelper = createColumnHelper();
  const [columnFilters, setColumnFilters] = useState([]);

  const convertRankStringToNum = (str) => {
    if (typeof str == "number") {
      return str;
    }

    if (!isNaN(parseFloat(str)) && isFinite(str)) {
      let x = str.split("+")[0];

      return parseInt(x);
    }

    return Infinity;
  };

  const rankSort = (rowA, rowB, columnId) => {
    return convertRankStringToNum(rowA.getValue(columnId)) <
      convertRankStringToNum(rowB.getValue(columnId))
      ? 1
      : -1;
  };

  const handleVideoUrl = async (item) => {
    try {
      let token = Cookies.get("access_token");

      let resp = await fetch(
        process.env.REACT_APP_API_URL +
          `/get_primary_assets_of_item?item_id=${item["LD ID"]}`,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      const { data } = await resp.json();

      // let x =
      //   "http://assets.liquid.diamonds/index.html?key=u2vhcfkgfwm9f01&key2=u915cirri7szf92&asseturl=https://npol.s3.eu-central-1.amazonaws.com/C/1447847306.PDF";

      const domain = new URL(data);
      if (domain.hostname == "assets.liquid.diamonds") {
        const urlParams = new URLSearchParams(data.split("?")[1]);

        const asseturl = urlParams.get("asseturl");

        setVideoUrl(asseturl);
      } else {
        setVideoUrl(data);
      }
    } catch (err) {
      console.log({ err });
    }
  };

  const columns = React.useMemo(
    () => [
      columnHelper.accessor("Cert Num", {
        id: "Cert Num",
        cell: (item) => {
          // width : 85 px

          return (
            <a href={item.row.original["Cert Num Url"]} target="_blank">
              {item.getValue()}
            </a>
          );
        },
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Shape", {
        id: "Shape",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Carats", {
        id: "Carats",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Color", {
        id: "Color",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Clarity", {
        id: "Clarity",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Cut", {
        id: "Cut",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Polish", {
        id: "Polish",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Sym", {
        id: "Sym",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Fluor", {
        id: "Fluor",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Shade", {
        id: "Shade",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Milky", {
        id: "Milky",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Lab", {
        id: "Lab",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Eye Clean", {
        id: "Eye Clean",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("BIC", {
        id: "BIC",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Rap %", {
        id: "Rap %",
        header: "Rap %",
        cell: ({ row, getValue }) => parseFloat(getValue()).toFixed(2),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("$/Ct", {
        id: "$/Ct",
        cell: ({ row, getValue }) => {
          return formatter.format(Math.round(getValue()));
        },
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Total Price", {
        id: "Total Price",
        cell: ({ row, getValue }) => {
          return formatter.format(Math.round(getValue()));
        },
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Video", {
        id: "Video",
        header: "Video",
        cell: (item) => {
          return (
            <div
              onClick={() => {
                openModal(true);
              }}
              style={{
                textDecorationLine: "underline",
                color: "blue",
              }}
            >
              {item.getValue()}
            </div>
          );
        },
        footer: (item) => item.column.id,
      }),
      columnHelper.group({
        id: "Rap",
        header: "Rap",

        columns: [
          columnHelper.accessor("Rap Rank", {
            id: "Rap Rank",
            header: "Rank",
            cell: ({ row, getValue }) => {
              // console.log({ rank: getValue(), type: typeof getValue() });
              if (typeof getValue() == "string") {
                if (getValue() !== "NA") {
                  let x = getValue().split("+")[0];

                  x = `>${x}`;

                  return x;
                }
                return "--";
              }

              return parseInt(getValue());
            },
            sortingFn: "rankSort",
            footer: (item) => item.column.id,
          }),
          columnHelper.accessor("Rap Matching count", {
            id: "Rap Matching count",
            header: "Count",
            cell: ({ row, getValue }) => getValue(),
            footer: (item) => item.column.id,
          }),
          columnHelper.accessor("1st Rap %", {
            id: "1st Rap %",
            header: "1st",

            cell: ({ row, getValue }) =>
              getValue() !== "" ? parseFloat(getValue()).toFixed(2) : "",
            footer: (item) => item.column.id,
          }),
          columnHelper.accessor("5th Rap %", {
            id: "5th Rap %",
            header: "5th",

            cell: ({ row, getValue }) =>
              getValue() !== "" ? parseFloat(getValue()).toFixed(2) : "",
            footer: (item) => item.column.id,
          }),
          columnHelper.accessor("25th Rap %", {
            id: "25th Rap %",
            header: "25th",

            cell: ({ row, getValue }) =>
              getValue() !== "" ? parseFloat(getValue()).toFixed(2) : "",
            footer: (item) => item.column.id,
          }),
          columnHelper.accessor("50th Rap %", {
            id: "50th Rap %",
            header: "50th",

            cell: ({ row, getValue }) =>
              getValue() !== "" ? parseFloat(getValue()).toFixed(2) : "",
            footer: (item) => item.column.id,
          }),
          columnHelper.accessor("100th Rap %", {
            id: "100th Rap %",
            header: "100th",

            cell: ({ row, getValue }) =>
              getValue() !== "" ? parseFloat(getValue()).toFixed(2) : "",
            footer: (item) => item.column.id,
          }),
        ],
      }),
      columnHelper.group({
        id: "Inv",
        header: "Your Inventory",
        columns: [
          columnHelper.accessor("LD Spec", {
            id: "LD Spec",
            cell: ({ row, getValue }) => getValue(),
            footer: (item) => item.column.id,
          }),
          columnHelper.accessor("LD Rank", {
            id: "LD Rank",
            header: "Rank",
            cell: ({ row, getValue }) => {
              if (typeof getValue() == "string") {
                let x = getValue().split("+")[0];

                x = `>${x}`;

                return x;
              }

              return getValue();
            },
            footer: (item) => item.column.id,
          }),
          columnHelper.accessor("LD Count", {
            id: "LD Count",
            header: "Count",
            cell: ({ row, getValue }) => getValue(),
            footer: (item) => item.column.id,
          }),
          columnHelper.accessor("aging", {
            id: "Aging",
            header: "Aging",
            cell: ({ row, getValue }) => getValue(),
            footer: (item) => item.column.id,
          }),
        ],
      }),

      columnHelper.accessor("Measurements", {
        id: "Measurements",
        header: "Measurements",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Table", {
        id: "Table",
        header: "Table",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Depth", {
        id: "Depth",
        header: "Depth",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Key To Symbol", {
        id: "Key To Symbol",
        header: "Key To Symbol",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Cert Comment", {
        id: "Cert Comment",
        header: "Cert Comments",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Girdle", {
        id: "Girdle",
        header: "Girdle",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("CrAng", {
        id: "CrAng",
        header: "CrAng",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("CrHeight", {
        id: "CrHeight",
        header: "CrHeight",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("PavAng", {
        id: "PavAng",
        header: "PavAng",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("PavDepth", {
        id: "PavDepth",
        header: "PavDepth",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Girdle Con", {
        id: "Girdle Con",
        header: "Girdle Con",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Culet", {
        id: "Culet",
        header: "Culet",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("BIS", {
        id: "BIS",
        header: "BIS",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("WIS", {
        id: "WIS",
        header: "WIS",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("WIC", {
        id: "WIC",
        header: "WIC",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("OT", {
        id: "OT",
        header: "OT",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("OC", {
        id: "OC",
        header: "OC",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("OP", {
        id: "OP",
        header: "OP",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Flr Clr", {
        id: "Flr Clr",
        header: "Flr Clr",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Location", {
        id: "Location",
        header: "Location",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Report Date", {
        id: "Report Date",
        header: "Report Date",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Ratio", {
        id: "Ratio",
        header: "Ratio",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
      columnHelper.accessor("Carat Range", {
        id: "Carat Range",
        header: "Carat Range",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.column.id,
      }),
    ],
    []
  );

  useEffect(() => {
    (async () => {
      // call  the fetch request for the table data here.

      try {
        let token = Cookies.get("access_token");

        let x = await fetch(
          process.env.REACT_APP_API_URL +
            `/get_pricing_report_items?seller_account_id=${id}`,
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );

        // let id = 880;

        // let x = await fetch(
        //   `https://app.liquid.diamonds/get_pricing_report_items?seller_account_id=${id}`,
        //   {
        //     headers: {
        //       Authorization: "Bearer d18198f276acab345ab6f3302b5a37a4",
        //     },
        //   }
        // );

        const { data } = await x.json();

        if (data.length < 1) {
          throw new Error("No Items in your co-pilot inventory");
        }
        setError(false);
        setItemData(data.pricing_report_items);
      } catch (err) {
        console.log(err);
        setError(true);
        setErrorMessage(err.message);
      }
    })();
  }, []);

  const table = useReactTable({
    data: itemData,
    columns,
    getCoreRowModel: getCoreRowModel(),

    state: {
      sorting,
      columnFilters,
    },
    sortingFns: {
      rankSort,
    },
    onSortingChange: setSorting,
    getSortedRowModel: getSortedRowModel(),
    getPaginationRowModel: getPaginationRowModel(),
    onColumnFiltersChange: setColumnFilters,
    // getExpandedRowModel: getExpandedRowModel(),
    getFilteredRowModel: getFilteredRowModel(),
    // enableColumnResizing: true,
    // columnResizeMode: 'onChange',
    // globalFilterFn: globalFilter,
    debugTable: true,
  });

  useEffect(() => {
    table.setPageSize("25");
  }, []);

  const calucateCurrentItems = () => {
    let totalItems = itemData.length;

    let currentPage = table.getState().pagination.pageIndex;
    let rowCount = table.getState().pagination.pageSize;

    let rangeStart = currentPage * rowCount + 1;
    let rangeEnd = rangeStart + rowCount;

    if (rangeEnd > totalItems) {
      rangeEnd = totalItems;
    }

    return `Showing ${rangeStart} - ${rangeEnd} out of ${totalItems} items`;
  };

  return (
    <Fragment>
      {itemData && !error ? (
        <div
          style={{
            overflow: "hidden",
            height: "100%",
            display: "flex",
            flexDirection: "column",
          }}
        >
          <div className="pagination-controls">
            <div
              style={{
                fontWeight: "bold",
                display: "flex",
                flexDirection: "row",
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              {" "}
              Your Diamonds
            </div>
            {console.log(table)}
            <SearchBar
              setSearchParam={(value) => {
                console.log(value);
                setColumnFilters([
                  {
                    id: "Cert Num",
                    value: value,
                  },
                ]);
              }}
            />
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <div style={{ marginRight: "14px" }}>
                {calucateCurrentItems()}
              </div>
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <button
                  className="btn-pn"
                  onClick={() => table.setPageIndex(0)}
                  disabled={!table.getCanPreviousPage()}
                >
                  {"<<"}
                </button>
                <button
                  className="btn-pn"
                  onClick={() => table.previousPage()}
                  disabled={!table.getCanPreviousPage()}
                >
                  {"<"}
                </button>
                <span style={{ margin: "0px 5px" }}>
                  <div>
                    {table.getState().pagination.pageIndex + 1} of{" "}
                    {table.getPageCount()}
                  </div>
                </span>
                <button
                  className="btn-pn"
                  onClick={() => table.nextPage()}
                  disabled={!table.getCanNextPage()}
                >
                  {">"}
                </button>
                <button
                  className="btn-pn"
                  onClick={() => table.setPageIndex(table.getPageCount() - 1)}
                  disabled={!table.getCanNextPage()}
                >
                  {">>"}
                </button>
              </div>
            </div>
          </div>
          <div
            style={{ overflow: "scroll" }}
            ref={passRef}
            className="table-cs"
          >
            <table style={{ borderTop: "0px", marginBottom: "0px" }}>
              <thead>
                {table.getHeaderGroups().map((headerGroup) => {
                  return (
                    <tr
                      key={headerGroup.id + Math.random()}
                      style={{
                        // borderLeft:
                        //   headerGroup.depth < 1 ? "none" : "0.5px solid black",
                        borderLeft: "0.5px solid black",
                        color: "black",
                      }}
                    >
                      {headerGroup.headers.map((header) => {
                        let borderRight;
                        let borderLeft;
                        let backgroundColor;

                        if (
                          header.column.id == "Rap" ||
                          header.column.id == "Inv"
                        ) {
                          borderRight = "0.5px solid black";
                          borderLeft = "0.5px solid black";

                          // backgroundColor = "aliceblue"
                        }

                        return (
                          <th
                            className="c-header"
                            key={header.id}
                            colSpan={header.colSpan}
                            style={{
                              textAlign: "center",
                              borderLeft:
                                headerGroup.depth < 1 ? borderLeft : "",
                              borderRight:
                                headerGroup.depth < 1
                                  ? borderRight
                                  : "0.5px solid black",
                              borderTop:
                                headerGroup.depth < 1
                                  ? "0.5px solid black"
                                  : "0.5px solid black",

                              borderBottom:
                                headerGroup.depth < 1
                                  ? "none"
                                  : "0.5px solid black",
                              backgroundColor,
                            }}
                          >
                            {header.isPlaceholder ? null : (
                              <div
                                {...{
                                  className: header.column.getCanSort()
                                    ? "cursor-pointer select-none"
                                    : "",
                                  onClick:
                                    header.column.getToggleSortingHandler(),
                                }}
                              >
                                {flexRender(
                                  header.column.columnDef.header,
                                  header.getContext()
                                )}
                                {{
                                  asc: " 🔼",
                                  desc: " 🔽",
                                }[header.column.getIsSorted()] ?? null}
                                {/* {header.column.getCanFilter() ? (

                                                                                        <div>
                                                                                            <Filter column={header.column} table={table} />
                                                                                        </div>
                                                                                        ) : null} */}
                              </div>
                            )}
                          </th>
                        );
                      })}
                    </tr>
                  );
                })}
              </thead>
              <tbody>
                {table.getRowModel().rows.map((row, idx) => (
                  <Fragment key={idx}>
                    <tr
                      key={row.id}
                      onClick={() => {
                        setItem(row.original);
                        setIntItem(row.original);
                        handleVideoUrl(row.original);
                      }}
                      style={{
                        cursor: "pointer",
                        backgroundColor: intItem
                          ? row.original["LD ID"] === intItem["LD ID"]
                            ? "#bde0ff"
                            : "white"
                          : "white",
                      }}
                    >
                      {row.getVisibleCells().map((cell) => {
                        // console.log(colsWidth[cell.column.id], cell.column.id)
                        return (
                          <td
                            key={cell.id}
                            className={` ${
                              rightAligned.includes(cell.column.id)
                                ? "txt-al-right"
                                : ""
                            }
                            
                            ${
                              rapDiff.includes(cell.column.id)
                                ? cell.getValue() !== ""
                                  ? cell.getValue() < row.original["Rap %"]
                                    ? "anq-wht"
                                    : "drksg"
                                  : ""
                                : ""
                            }
                            
                            `}
                            style={{
                              minWidth:
                                colsWidth[cell.column.id] !== undefined
                                  ? colsWidth[cell.column.id]
                                  : "auto",
                              maxWidth: colsWidth[cell.column.id]
                                ? colsWidth[cell.column.id]
                                : "auto",
                              fontWeight:
                                cell.column.id == "Rap Rank" ? "bold" : "",
                            }}
                          >
                            {flexRender(
                              cell.column.columnDef.cell,
                              cell.getContext()
                            )}
                          </td>
                        );
                      })}
                    </tr>
                  </Fragment>
                ))}
              </tbody>
              {/* <tfoot>
          {table.getFooterGroups().map(footerGroup => (
            <tr key={footerGroup.id}>
              {footerGroup.headers.map(header => (
                <th key={header.id} colSpan={header.colSpan}>
                  {header.isPlaceholder
                    ? null
                    : flexRender(
                        header.column.columnDef.footer,
                        header.getContext()
                      )}
                </th>
              ))}
            </tr>
          ))}
        </tfoot> */}
            </table>
          </div>
        </div>
      ) : error ? (
        <div
          style={{ display: "flex", width: "100%", justifyContent: "center" }}
        >
          {errorMessage}
        </div>
      ) : (
        <div
          style={{
            width: "100%",
            height: "100%",
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Spinner />
          <br></br>
          <div
            style={{
              fontWeight: "bold",
            }}
          >
            Fetching Items
          </div>
        </div>
      )}
    </Fragment>
  );
};

