
export const DataReducer = (state, action) => {

    switch (action.type) {

        case "Update_Inventory":
            return [
                ...state,
                {
                    "inventory": action.payload
                }
            ]

        case "Update_Sale":
            return [
                ...state,
                {
                    "sale": action.payload
                }
            ]

        case "Update_Aging":
            return [
                ...state,
                {
                    "aging": action.payload
                }
            ]

        default:
            return state

    }
}   