import React, { useEffect, useRef, useState, Fragment } from "react";
import {
  useReactTable,
  createColumnHelper,
  flexRender,
  getCoreRowModel,
  getSortedRowModel,
  //   getPaginationRowModel,
  //   getExpandedRowModel,
  //   getFilteredRowModel,
} from "@tanstack/react-table";
import Spinner from "react-bootstrap/Spinner";
import { filters } from "./filterData";
import "./BottomTable.css";

import Cookies from "js-cookie";
import moment from "moment";
import RankingSummary from "../RankingSummary/RankingSummary";
import { formatter } from "../../utils/utils";

const colsWidth = {
  "Cert Num": "85px",
  Shape: "60px",
  Carats: "60px",
  "LD Spec": "60px",
  Color: "60px",
  Clarity: "60px",
  Cut: "60px",
  Polish: "60px",
  Sym: "60px",
  Fluor: "60px",
  Shade: "60px",
  Milky: "60px",
  Lab: "60px",
  "Eye Clean": "80px",
  BIC: "60px",
  "Rap $/Ct": "60px",
  "$/Ct": "60px",
  "Total Price": "75px",
  Video: "60px",
  "Rap Rank": "60px",
  "Stock No": "300px",
  "Supplier Comment": "180px",
  "Cert Comment": "120px",

  "Girdle Con": "85px",
  "Report Date": "85px",
  "Carat Range": "85px",
};

const rightAligned = [
  "Carats",
  "Rap %",
  "$/Ct",
  "Total Price",
  "LD Rank",
  "Rap Rank",
  "Rap Matching count",
  "1st Rap %",
  "5th Rap %",
  "25th Rap %",
  "50th Rap %",
  "100th Rap %",
  "Aging",
  "CrAng",
  "CrHeight",
  "PavAng",
  "PavDepth",
  "Ratio",
  "Carat Range",
  "Depth",
  "Table",
];

const BottomTable = ({
  rowData,
  passRef,
  scroll,
  openModal,
  bottomItem,
  bottomData,
}) => {
  // const [columns, setColumns] = useState()
  const [itemData, setItemData] = useState();
  const [sorting, setSorting] = useState();
  const selectedItemRef = useRef();
  const columnHelper = createColumnHelper();
  const [error, setError] = useState(false);
  const [errorMessage, setErrorMessage] = useState();
  const [rapFilter, setRapFilter] = useState({});

  const columns = React.useMemo(
    () => [
      columnHelper.accessor("certificate #", {
        id: "Cert Num",
        header: "Cert Num",
        cell: (item) => {
          return (
            <a
              href={
                item.row.original["cert url"] == ""
                  ? `https://www.gia.edu/report-check?reportno=${item.getValue()}`
                  : item.row.original["cert url"]
              }
              target="_blank"
            >
              {item.getValue()}
            </a>
          );
        },
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("shape", {
        id: "Shape",
        header: "Shape",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("carat", {
        id: "Carats",
        header: "Carat",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("color", {
        id: "Color",
        header: "Color",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("clarity", {
        id: "Clarity",
        header: "Clarity",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("cut", {
        id: "Cut",
        header: "Cut",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("polish", {
        id: "Polish",
        header: "Polish",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("symmetry", {
        id: "Sym",
        header: "Sym",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("fluor", {
        id: "Fluor",
        header: "Fluor",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("shade", {
        id: "Shade",
        header: "Shade",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("milky", {
        id: "Milky",
        header: "Milky",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("lab", {
        id: "Lab",
        header: "Lab",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("eyeclean", {
        id: "Eye Clean",
        header: "Eye Clean",

        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("BIC", {
        id: "BIC",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("Rap%", {
        id: "Rap %",
        cell: ({ row, getValue }) => parseFloat(getValue()).toFixed(2),
      }),
      columnHelper.accessor("price_per_carat", {
        id: "$/Ct",
        header: "$/Ct",
        cell: ({ row, getValue }) => {
          return formatter.format(Math.round(getValue()));
        },
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("total_price", {
        id: "Total Price",
        header: "Total Price",

        cell: ({ row, getValue }) => {
          return formatter.format(Math.round(getValue()));
        },
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("videolink", {
        id: "Video",
        header: "Video",
        cell: (item) => {
          if (item.row.original["videolink"]) {
            return (
              <div
                onClick={() => {
                  console.log({ row: item.row });
                  bottomData(itemData);
                  bottomItem(item.row.index);
                  openModal(true);
                }}
                style={{
                  textDecorationLine: "underline",
                  color: "blue",
                }}
              >
                video
              </div>
            );
          } else if (item.row.original["imagename1"]) {
            return (
              <div
                onClick={() => {
                  console.log({ row: item.row });
                  bottomItem(item.row.index);
                  openModal(true);
                }}
                style={{
                  textDecorationLine: "underline",
                  color: "blue",
                }}
              >
                video
              </div>
            );
          }
          return "";
        },
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("Rap Rank", {
        id: "Rap Rank",
        header: "Rank",
        cell: ({ row, getValue }) => {
          return row.index + 1;
        },
        sortingFn: "myCustomSorting",
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("Stock No", {
        id: "Stock No",
        header: "Supplier",
        //width : 300px
        cell: ({ row, getValue }) => {
          let x = getValue();

          let y = x.split("|")[0];

          return y;
        },
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("member comment", {
        id: "Supplier Comment",
        header: "Supplier Comment",
        cell: ({ row, getValue }) => <div>{getValue()}</div>,
        footer: (item) => item.getValue(),
      }),

      columnHelper.accessor("Measurements", {
        id: "Measurements",
        header: "Measurements",
        cell: ({ row, getValue }) => {
          return (
            <div>
              {`${row.original["length"]} x ${row.original["width"]} x ${row.original["height"]}`}
            </div>
          );
        },
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("table %", {
        id: "Table",
        header: "Table",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("depth %", {
        id: "Depth",
        header: "Depth",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("key to symbols", {
        id: "Key To Symbol",
        header: "Key To Symbol",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("cert comment", {
        id: "Cert Comment",
        header: "Cert Comments",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("girdle min", {
        id: "Girdle",
        header: "Girdle",
        cell: ({ row, getValue }) => {
          return (
            <div>
              {`${row.original["girdle min"]} - ${row.original["girdle max"]}`}
            </div>
          );
        },
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("CA", {
        id: "CrAng",
        header: "CrAng",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("CH", {
        id: "CrHeight",
        header: "CrHeight",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("PA", {
        id: "PavAng",
        header: "PavAng",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("PD", {
        id: "PavDepth",
        header: "PavDepth",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("girdle condition", {
        id: "Girdle Con",
        header: "Girdle Con",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("culet size", {
        id: "Culet",
        header: "Culet",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("BIS", {
        id: "BIS",
        header: "BIS",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("WIS", {
        id: "WIS",
        header: "WIS",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("WIC", {
        id: "WIC",
        header: "WIC",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("OT", {
        id: "OT",
        header: "OT",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("OC", {
        id: "OC",
        header: "OC",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("OP", {
        id: "OP",
        header: "OP",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("fluor color", {
        id: "Flr Clr",
        header: "Flr Clr",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("country", {
        id: "Location",
        header: "Location",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("report date", {
        id: "Report Date",
        header: "Report Date",
        cell: ({ row, getValue }) => {
          let x = moment.utc(getValue()).format("MM/DD/YYYY");

          return x;
        },
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("Ratio", {
        id: "Ratio",
        header: "Ratio",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
      columnHelper.accessor("Carat Range", {
        id: "Carat Range",
        header: "Carat Range",
        cell: ({ row, getValue }) => getValue(),
        footer: (item) => item.getValue(),
      }),
    ],
    [rowData]
  );

  useEffect(() => {
    if (selectedItemRef.current) {
      selectedItemRef.current.scrollIntoView({
        behavior: "auto",
        block: "center",
      });
    }
  }, [selectedItemRef.current]);

  useEffect(() => {
    (async () => {
      // call the fetch request for the table data here.
      try {
        setItemData(null);

        let token = Cookies.get("access_token");

        // let x = await fetch(
        //   `https://app.liquid.diamonds/get_rapnet_matching_items_for_item?item_id=${rowData["LD ID"]}`,
        //   {
        //     headers: {
        //       Authorization: `Bearer d18198f276acab345ab6f3302b5a37a4`,
        //     },
        //   }
        // );

        let x = await fetch(
          process.env.REACT_APP_API_URL +
            `/get_rapnet_matching_items_for_item?item_id=${rowData["LD ID"]}`,
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );

        const { data } = await x.json();

        if (data.rapnet_matching_item_details.length < 1) {
          throw new Error("No similar items found in rapnet");
        }

        setRapFilter(data.rapnet_search_filter_set);

        setItemData(data.rapnet_matching_item_details);
        bottomData(data.rapnet_matching_item_details);
        setError(false);
      } catch (err) {
        console.log(err);
        setError(true);
        setErrorMessage(err.message);
      }
    })();
  }, [rowData]);

  const table = useReactTable({
    data: itemData,
    columns,
    getCoreRowModel: getCoreRowModel(),

    state: {
      sorting,
    },
    sortingFns: {
      myCustomSorting: (rowA, rowB, columnId) => {
        return parseInt(rowA.id) < parseInt(rowB.id) ? 1 : -1;
      },
    },
    onSortingChange: setSorting,
    getSortedRowModel: getSortedRowModel(),
    debugTable: true,
  });

  return (
    <>
      {itemData && !error ? (
        <Fragment>
          <div className="filter-scroll">
            <div className="filters-Container">
              {Object.entries(rapFilter).map(([key, value]) => (
                <Fragment key={key}>
                  <div className="filter-label">
                    <div className="filter-key">{`${key}: `} &nbsp;</div>

                    <div className="filter-value"> {value.toString()}</div>
                  </div>
                </Fragment>
              ))}
            </div>
          </div>
          <div
            style={{
              overflow: "hidden",
              height: "90%",
              display: "flex",
              flexDirection: "column",
              paddingBottom: "8px",
            }}
          >
            <div
              style={{ overflow: "scroll", display: "flex" }}
              ref={passRef}
              onScroll={scroll}
              className="table-cs"
            >
              <table style={{ borderTop: "none", height: "100%" }}>
                <thead>
                  {table.getHeaderGroups().map((headerGroup) => (
                    <tr key={headerGroup.id + Math.random()} style={{}}>
                      {headerGroup.headers.map((header, index) => {
                        // TODO:  color set should be here
                        // write a function based on header.column.id and set with a switch statement
                        return (
                          <th
                            className="c-header"
                            key={header.id}
                            colSpan={header.colSpan}
                            style={{
                              textAlign: "center",
                              borderLeft:
                                index > 0 ? "0.5px solid black" : "none",
                              borderTop: "0.5px solid black",
                              borderBottom: "0.5px solid black",
                            }}
                          >
                            {header.isPlaceholder ? null : (
                              <>
                                <div
                                  {...{
                                    className: header.column.getCanSort()
                                      ? "cursor-pointer select-none"
                                      : "",
                                    onClick:
                                      header.column.getToggleSortingHandler(),
                                  }}
                                >
                                  {flexRender(
                                    header.column.columnDef.header,
                                    header.getContext()
                                  )}
                                  {{
                                    asc: " 🔼",
                                    desc: " 🔽",
                                  }[header.column.getIsSorted()] ?? null}
                                </div>
                              </>
                            )}
                          </th>
                        );
                      })}
                    </tr>
                  ))}
                </thead>
                <tbody>
                  {table.getRowModel().rows.map((row, idx) => (
                    <Fragment key={idx}>
                      <tr
                        key={row.id}
                        style={{
                          cursor: "pointer",
                          backgroundColor:
                            row.original["certificate #"] == rowData["Cert Num"]
                              ? "#bde0ff"
                              : "white",
                        }}
                        ref={
                          row.original["certificate #"] == rowData["Cert Num"]
                            ? selectedItemRef
                            : null
                        }
                      >
                        {row.getVisibleCells().map((cell) => {
                          return (
                            <td
                              key={cell.id}
                              className={`overflow ${
                                rightAligned.includes(cell.column.id)
                                  ? "txt-al-right"
                                  : ""
                              }`}
                              style={{
                                minWidth:
                                  colsWidth[cell.column.id] !== undefined
                                    ? colsWidth[cell.column.id]
                                    : "auto",
                                maxWidth: colsWidth[cell.column.id]
                                  ? colsWidth[cell.column.id]
                                  : "auto",
                                fontWeight:
                                  cell.column.id == "Rap Rank" ? "bold" : "",
                              }}
                            >
                              {flexRender(
                                cell.column.columnDef.cell,
                                cell.getContext()
                              )}
                            </td>
                          );
                        })}
                      </tr>
                    </Fragment>
                  ))}
                </tbody>
              </table>
              <RankingSummary
                data={rowData}
                openModal={openModal}
                bottomData={bottomData}
              />
            </div>
          </div>
        </Fragment>
      ) : error ? (
        <div
          style={{
            display: "flex",
            width: "100%",
            justifyContent: "center",
            marginTop: "20px",
          }}
        >
          {errorMessage}
        </div>
      ) : (
        <div
          style={{
            width: "100%",
            height: "100%",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            flexDirection: "column",
          }}
        >
          <Spinner />
          <br></br>
          <div
            style={{
              fontWeight: "bold",
            }}
          >
            Fetching Items
          </div>
        </div>
      )}
    </>
  );
};

export default BottomTable;
