import React, {
  Fragment,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import TopTable from "../TopTable/TopTable";
import "./PageContainer.css";
import BottomTable from "../BottomTable/BottomTable";
import Modal from "react-bootstrap/Modal";
import ReactPlayer from "react-player/lazy";
import Cookies from "js-cookie";
import Carousel from "react-bootstrap/Carousel";

const PageContainer = ({ accountDetails }) => {
  const [selectedItem, setSelectedItem] = useState();
  const [openModal, setOpenModal] = useState(false);
  const [bottomData, setBottomData] = useState([]);
  const [index, setIndex] = useState(-1);
  const [videoUrl, setVideoUrl] = useState("");

  const topRef = useRef();
  const bottomRef = useRef();

  const scrollPos = () => {
    let x = bottomRef.current.scrollLeft;

    let bWidth = bottomRef.current.scrollWidth - bottomRef.current.offsetWidth;
    let bScrollPercent = (x / bWidth) * 100;

    let y =
      ((topRef.current.scrollWidth - topRef.current.offsetWidth) *
        bScrollPercent) /
      100;

    topRef.current.scrollLeft = y;
  };

  const handleClose = () => {
    setIndex(-1);
    setOpenModal(false);
  };

  const handleLoad = (x) => {
    console.log(x);
  };

  const checkVideoType = (str) => {
    console.log(str);
    const domain = new URL(str);
    const hostname = domain.hostname;
    if (hostname === "diamonds.kirangems.com") {
      const urlParams = new URLSearchParams(str.split("?")[1]);
      const ReportNo = urlParams.get("ReportNo");
      const StoneNo = urlParams.get("StoneNo");

      return (
        <iframe
          loading="lazy"
          sandbox="allow-same-origin allow-scripts allow-popups allow-forms"
          target="_blank"
          style={{
            height: "100%",
            width: "100%",
            overflow: "hidden",
          }}
          onClick={handleClose}
          src={`https://diamonds.kirangems.com/GemKOnline/DiaSearch/ImageZoomN.jsp?ReportNo=${ReportNo}&StoneNo=${StoneNo}`}
        ></iframe>
      );
    }

    if (hostname === "www.ankitgems.com") {
      const items = str.split("/");
      const i = items[items.length - 1];

      return (
        // <a href={str} target="_blank">
        //   Click to Open the Diamond Page.
        // </a>
        <iframe
          loading="lazy"
          sandbox="allow-same-origin allow-scripts allow-popups allow-forms"
          target="_blank"
          style={{
            height: "100%",
            width: "100%",
            overflow: "hidden",
          }}
          onClick={handleClose}
          src={`https://d1wzmxdlubs910.cloudfront.net/VIDEOS/${i}.html`}
        ></iframe>
      );
    }

    if (str) {
      switch (str) {
        case str.includes(".mp4"):
          return (
            <ReactPlayer
              controls={true}
              url={str}
              style={{
                width: "100%",
              }}
            />
          );

        default:
          if (str.toLowerCase().includes(".pdf")) {
            return (
              <a href={str} target="_blank">
                Click to Open the Diamond Page.
              </a>
            );
          }
          return (
            <iframe
              src={str}
              loading="lazy"
              sandbox="allow-same-origin allow-scripts allow-popups allow-forms"
              target="_blank"
              style={{
                height: "100%",
                width: "100%",
                overflow: "hidden",
              }}
              onClick={handleClose}
              onLoad={handleLoad}
            ></iframe>
          );
      }
    }
  };

  return accountDetails ? (
    <div style={{ width: "100%", height: "calc(100vh - 100px)" }}>
      <Modal show={openModal} onHide={handleClose} fullscreen="true">
        <Modal.Header closeButton>
          {index >= 0 ? (
            <div className="Modal-Header">
              <div className="left-modalTitle">{`Your Diamond  (LD Spec - ${selectedItem["LD Spec"]}, ${selectedItem["Rap Rank"]}/${selectedItem["Rap Matching Count"]})`}</div>
              <div className="right-modalTitle">
                {`${
                  bottomData[index]["Stock No"]
                    ? bottomData[index]["Stock No"].split("|")[0]
                    : ""
                }, ${index + 1}/${selectedItem["Rap Matching Count"]}`}
              </div>
            </div>
          ) : (
            ""
          )}
        </Modal.Header>

        <Modal.Body style={{ overflow: "scroll" }}>
          {index >= 0 ? (
            <div
              style={{
                width: "100%",
                height: "100%",
                overflow: "hidden",
                display: "flex",
                flexDirection: "row",
              }}
            >
              <div className="left-ModalVideoContainer">
                {selectedItem ? (
                  <div className="Modal-Container">
                    <div
                      className="left-ModalHeader"
                      style={{ marginBottom: "10px", textAlign: "center" }}
                    >
                      {`${selectedItem["Shape"]} ${selectedItem["Carats"]} ${
                        selectedItem["Color"]
                      } ${selectedItem["Clarity"]} ${
                        selectedItem["Cut"] !== null ? selectedItem["Cut"] : ""
                      } ${selectedItem["Polish"]} ${selectedItem["Sym"]} ${
                        selectedItem["Fluor"]
                      }`}
                      <br />
                      {`${
                        selectedItem["Key To Symbol"]
                          ? selectedItem["Key To Symbol"]
                          : ""
                      } @ ${Math.round(selectedItem["Rap %"])}`}
                      <br />
                      {`${
                        selectedItem["Measurements"]
                          ? selectedItem["Measurements"]
                          : ""
                      }, T%: ${
                        selectedItem["Table"] ? selectedItem["Table"] : ""
                      }, D%: ${
                        selectedItem["Depth"] ? selectedItem["Depth"] : ""
                      }, ${selectedItem["Ratio"] ? selectedItem["Ratio"] : ""}`}
                    </div>
                    <div className="Modal-ContentContainer">
                      {/* {checkVideoType(selectedItem["Video Url"])} */}
                      {videoUrl ? checkVideoType(videoUrl) : ""}
                    </div>
                  </div>
                ) : (
                  ""
                )}
              </div>
              <div className="right-ModalVideoContainer">
                {index >= 0 ? (
                  <Carousel
                    variant="dark"
                    interval={null}
                    activeIndex={index}
                    onSelect={(idx) => setIndex(idx)}
                    style={{
                      height: "100%",
                    }}
                    indicators={false}
                  >
                    {bottomData.map((item) => {
                      return (
                        <Carousel.Item>
                          <div className="Modal-Container">
                            <div
                              className="right-ModalHeader"
                              style={{
                                marginBottom: "10px",
                                textAlign: "center",
                              }}
                            >
                              {`${item["shape"]} ${item["carat"]} ${
                                item["color"]
                              } ${item["clarity"]} ${
                                item["cut"] ? item["cut"] : ""
                              } ${item["polish"]} ${item["symmetry"]} 
                    ${item["fluor"]}`}
                              <br />
                              {`${item["key to symbols"]} ${
                                item["Rap%"]
                                  ? `@ ${Math.round(item["Rap%"])}`
                                  : ""
                              }`}
                              <br />
                              {` ${item["length"] ?? "__"}x  ${
                                item["width"] ?? "__"
                              } x  ${item["height"] ?? "__"}, T%: ${
                                item["table %"] ?? ""
                              }, D%: ${item["depth %"]}, ${
                                item["Ratio"] ? item["Ratio"] : ""
                              }`}
                            </div>
                            <div className="Modal-ContentContainer">
                              {item["videolink"] ? (
                                checkVideoType(item["videolink"])
                              ) : item["imagename1"] ? (
                                checkVideoType(item["imagename1"])
                              ) : (
                                <img
                                  src="assets/img/no-image.png"
                                  style={{
                                    margin: "auto",
                                  }}
                                />
                              )}
                            </div>
                          </div>
                        </Carousel.Item>
                      );
                    })}
                  </Carousel>
                ) : (
                  ""
                )}
              </div>
            </div>
          ) : selectedItem ? (
            <Fragment>
              <div
                className="left-ModalHeader"
                style={{ marginBottom: "10px", textAlign: "center" }}
              >
                {`${selectedItem["Shape"]} ${selectedItem["Carats"]} ${
                  selectedItem["Color"]
                } ${selectedItem["Clarity"]} ${
                  selectedItem["Cut"] !== null ? selectedItem["Cut"] : ""
                } ${selectedItem["Polish"]} ${selectedItem["Sym"]} ${
                  selectedItem["Fluor"]
                }`}
                <br />
                {`${selectedItem["Key To Symbol"]} @ ${Math.round(
                  selectedItem["Rap %"]
                )}`}
              </div>
              <div className="Modal-ContentContainer">
                {/* {getVideoUrl(selectedItem)} */}
                {/* {checkVideoType(selectedItem["Video Url"])} */}
                {videoUrl ? checkVideoType(videoUrl) : ""}
              </div>
            </Fragment>
          ) : (
            ""
          )}
        </Modal.Body>
      </Modal>
      <div className="topTable-container">
        <TopTable
          setItem={setSelectedItem}
          passRef={topRef}
          id={accountDetails.account_id}
          openModal={setOpenModal}
          bottomItem={setIndex}
          setVideoUrl={setVideoUrl}
        />
      </div>

      <div className="bottomTable-container">
        {selectedItem ? (
          <BottomTable
            rowData={selectedItem}
            passRef={bottomRef}
            scroll={scrollPos}
            openModal={setOpenModal}
            bottomItem={setIndex}
            bottomData={setBottomData}
          />
        ) : (
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignContent: "center",
            }}
          >
            {/* <h2>Select a item to display similar diamonds</h2> */}
          </div>
        )}
      </div>
    </div>
  ) : (
    ""
  );
};

export default PageContainer;
