import React from "react";
import { ColorRing } from "react-loader-spinner";

const TurnReport = ({ DataisLoaded, items }) => {
  // function to get background color between red to green based on values
  const getBgColor = (min, max, value) => {
    let rgb_string = "";
    if (value === max) {
      rgb_string = "rgba(100,255,90,0.7)";
    } else if (value === min) {
      rgb_string = "rgba(255,100,90,0.7)";
    } else {
      const range = max - min;
      const percentage = parseInt(((value - min) / range) * 100);
      let red, green;
      if (percentage < 50) {
        // get color between red to yellow
        red = 155 + (100 - percentage);
        green = 155 * (percentage / 50) + 100;
      } else {
        // get color between yellow to green
        red = (155 * (100 - percentage)) / 50 + 100;
        green = 155 + percentage;
      }
      rgb_string = `rgba(${red},${green},90,0.7)`;
    }
    return rgb_string;
  };
  return (
    <>
      {!DataisLoaded ? (
        <div className="row">
          <ColorRing
            visible={true}
            height="60"
            width="60"
            ariaLabel="blocks-loading"
            wrapperStyle={{}}
            wrapperClass="blocks-wrapper"
            colors={["#00319f", "#2e56b1", "#5c7bc2", "#8aa1d3", "#b2c1e2"]}
          />
          <div className="align-items-center justify-content-center">
            <h3 className=" text-center"> Please wait... </h3>
          </div>
        </div>
      ) : (
        <div class="row">
          {Object.entries(items).map(([key, value]) => {
            // Getting min max amounts of each tables
            const max = Math.max(
              ...value.map((e) =>
                parseInt(e.total_amount.slice(1, -3).split(",").join(""))
              )
            );
            const min = Math.min(
              ...value.map((e) =>
                parseInt(e.total_amount.slice(1, -3).split(",").join(""))
              )
            );
            return (
              <div class="col-xs-12 col-sm-12 col-md-7 col-lg-6 col-xl-4">
                <h5
                  className="card-title"
                  style={{ display: "flex", justifyContent: "center" }}
                >
                  {key.toUpperCase()}
                </h5>

                <div
                  class="card"
                  style={{
                    minHeight: "480px",
                    maxHeight: "480px",
                    overflow: "auto",
                  }}
                >
                  <div class="card-body">
                    <table class="table table-sm table-hover">
                      <thead
                        className="sticky-top bg-white"
                        style={{
                          top: -1,
                          zIndex: 100,
                        }}
                      >
                        <tr>
                          <th scope="col">Name</th>
                          <th scope="col">Inv Count</th>
                          <th scope="col">Inv Value</th>
                          <th scope="col">Sale Count</th>
                          <th scope="col">Sale Value</th>
                          {/* <th scope="col">Months to Rotate</th> */}
                          <th scope="col">Turn</th>
                        </tr>
                      </thead>
                      <tbody>
                        {value.map((element, idx) => {
                          return (
                            <tr key={idx}>
                              <td scope="row" style={{ textAlign: "left" }}>
                                {element.name}
                              </td>
                              <td scope="row">{element.count}</td>
                              <td
                                scope="row"
                                style={{
                                  backgroundColor: `${getBgColor(
                                    min,
                                    max,
                                    parseInt(
                                      element.total_amount
                                        .slice(1, -3)
                                        .split(",")
                                        .join("")
                                    )
                                  )}`,
                                  opacity: 1,
                                }}
                              >
                                {element.total_amount.slice(0, -3)}
                              </td>
                              <td scope="row">{element.sale_count}</td>
                              <td scope="row">
                                {element.sale_total_amount.slice(0, -3)}
                              </td>
                              {/* <td scope='row'>{element.num_months_to_rotate}</td> */}
                              <td scope="row">{element.turn}</td>
                            </tr>
                          );
                        })}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      )}
    </>
  );
};

export default TurnReport;
